import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Message, MessageService } from 'primeng/api';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { environment } from 'src/environments/environment.prod';


@Component({
  selector: 'app-recherche',
  templateUrl: './recherche.component.html',
  styleUrls: ['./recherche.component.css'],
  providers:[MessageService]
})
export class RechercheComponent implements OnInit {

  /** 
   * VARIABLE
   */
    private msgs:Message[] = [];
    rechercheForm: FormGroup; 

    private string:any= undefined;
    private  article :any = undefined;

    private historiques:any = undefined;
    private alarmes:any = undefined;

    private parametre:any = undefined;


    /**
     * 
     * @param formBuilder 
     * @param messageService 
     * @param httpClient 
     * @param router 
     * @param tokenStorage 
     */

    constructor(private formBuilder: FormBuilder, private messageService: MessageService, private httpClient:HttpClient, private router:Router, 
      private tokenStorage:TokenStorageService,  private activeroute:ActivatedRoute)  { }

    ngOnInit(): void {

      this.activeroute.queryParams
      .subscribe(params =>{
        this.parametre=params.id;  
        this.serchByreferenceReceiveinparam(this.parametre);      
      });

      this.rechercheForm = this.formBuilder.group({
        'reference': new FormControl('', Validators.required),
  
      });

    }

    serchByreferenceReceiveinparam(value:any)
    {

       this.httpClient.get(environment.url+'/api/postal/reception/stock/recherche/resultat?param='+value,  {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe( response =>{
        console.log(response);
        this.article = response;
        this.showInfo("Le chargement de l'information c'est deroulé avec success");
      }, error=>{
          this.showWarn("Une erreur c'est produit lors du chargement de l'information, veuillez contatcter l'administrateur systeme  et voici l'erreur  "+error.message);
      });

      this.httpClient.get(environment.url+'/api/postal/reception/historique?param='+value,  {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe( response =>{
        console.log(response);
        this.historiques = response;
        this.showInfo("Le chargement de l'information c'est deroulé avec success");
      }, error=>{
          this.showWarn("Une erreur c'est produit lors du chargement de l'information, veuillez contatcter l'administrateur systeme  et voici l'erreur  "+error.message);
      });

      this.httpClient.get(environment.url+'/api/postal/reception/alarme?param='+value,  {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe( response =>{
        console.log(response);
        this.alarmes = response;
        this.showInfo("Le chargement de l'information c'est deroulé avec success");
      }, error=>{
          this.showWarn("Une erreur c'est produit lors du chargement de l'information, veuillez contatcter l'administrateur systeme  et voici l'erreur  "+error.message);
      });

    }


    /**
     * Fonction pour recherche une reference
     * @param event 
     */
    searchByReference(event)
    {
      console.log(event)
       this.httpClient.get(environment.url+'/api/postal/reception/stock/recherche?param='+event.query,  {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe
       (
           response =>{

            console.log(response);
               this.string =response;
           }, err=>{
               this.showError("La recherche n'est pas operationnel car une erreur c'est produit. Veuillez verifier que tout les services sont operationnels.*** voici l'erreur generer par le systeme :"+err.message);
           }
       );
    }

    findResultat(reference:any)
    {
      this.httpClient.get(environment.url+'/api/postal/reception/stock/recherche/resultat?param='+reference['reference'],  {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe( response =>{
        console.log(response);
        this.article = response;
        this.showInfo("Le chargement de l'information c'est deroulé avec success");
      }, error=>{
          this.showWarn("Une erreur c'est produit lors du chargement de l'information, veuillez contatcter l'administrateur systeme  et voici l'erreur  "+error.message);
      });

      this.httpClient.get(environment.url+'/api/postal/reception/historique?param='+reference['reference'],  {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe( response =>{
        console.log(response);
        this.historiques = response;
        this.showInfo("Le chargement de l'information c'est deroulé avec success");
      }, error=>{
          this.showWarn("Une erreur c'est produit lors du chargement de l'information, veuillez contatcter l'administrateur systeme  et voici l'erreur  "+error.message);
      });


      this.httpClient.get(environment.url+'/api/postal/reception/alarme?param='+reference['reference'],  {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      }).subscribe( response =>{
        console.log(response);
        this.alarmes = response;
        this.showInfo("Le chargement de l'information c'est deroulé avec success");
      }, error=>{
          this.showWarn("Une erreur c'est produit lors du chargement de l'information, veuillez contatcter l'administrateur systeme  et voici l'erreur  "+error.message);
      });


    }



    /**
   *  costumisation des erreurs
   */

     showSuccess(message:string) {
      this.msgs = [];
      this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
    }
  
    showInfo(message:string) {
      this.msgs = [];
      this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
    }
  
    showWarn(message:string) {
      this.msgs = [];
      this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
    }
  
    showError(message:string) {
      this.msgs = [];
      this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
    }

}
