import { formatDate } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Message, MessageService } from 'primeng/api';
import { Dropdown } from 'src/app/apps/model/dropdown';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { Select } from 'src/app/model/Select';
import { environment } from 'src/environments/environment.prod';



@Component({
  selector: 'app-tableaubord',
  templateUrl: './tableaubordreception.component.html',
  styleUrls: ['./tableaubordreception.component.css'],
  providers:[MessageService]
})
export class TableaubordreceptionComponent implements OnInit {

  /***
   * 
   */

   msgs:Message[] = []; 
  
   tableaubord1:any = undefined;
   basicData:any =undefined;
   listems:any = undefined;



  /**
   * 
   * @param formBuilder 
   * @param messageService 
   * @param httpClient 
   * @param router 
   * @param tokenStorage 
   */
  constructor(private formBuilder: FormBuilder, private messageService: MessageService, private httpClient:HttpClient, private router:Router, 
    private tokenStorage:TokenStorageService)  { }


    
  ngOnInit(): void {

 
    this.httpClient.get(environment.url+"/api/postal/envoi/tableau/bord1",{headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe(response=>{
      console.log(response);
      this.tableaubord1 = {
        labels: ['A','B','C'],
        datasets: [
            {
                data: [300, 50, 100],
                backgroundColor: [
                    "#42A5F5",
                    "#66BB6A",
                    "#FFA726"
                ],
                hoverBackgroundColor: [
                    "#64B5F6",
                    "#81C784",
                    "#FFB74D"
                ]
            }
        ]
    };
    }, error=>{
        this.showWarn(" une erreur c'est produit et le système selectionner le type de ventes - La raison est voici : "+error.message);
    });


    this.basicData = {
      labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
      datasets: [
          {
              label: 'First Dataset',
              data: [65, 59, 80, 81, 56, 55, 40],
              fill: false,
              borderColor: '#42A5F5',
              tension: .4
          },
          {
              label: 'Second Dataset',
              data: [28, 48, 40, 19, 86, 27, 90],
              fill: false,
              borderColor: '#FFA726',
              tension: .4
          }
      ]
    };

        /**
     *  -- REQUETE POUR RECUPERER LA LISTE DES TYPE D ARTICLE
     */
    this.httpClient.get(environment.url+"/api/postal/reception/all", {headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe(response=>{
      this.listems = response;
      console.log(this.listems);
    }, error=>{      
      this.showWarn("Les articles EMS  n'ont pas pu etre chargé, Voici la raison "+error.getMessage());
    });

  }

  
  /**
   *  costumisation des erreurs
   */

   showSuccess(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
  }

  showInfo(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
  }

  showWarn(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
  }

  showError(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
  }



}