import { formatDate } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Message, MessageService } from 'primeng/api';
import { Dropdown } from 'src/app/apps/model/dropdown';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { Select } from 'src/app/model/Select';
import { environment } from 'src/environments/environment.prod';


export interface Vente{
  id?,
  typevente?,
  itemstypevente?,
  
  datevente?,
	nom?,
	prix?,

	penalite?,	
	createdat?,	
	updatedat?,

}



@Component({
  selector: 'app-nouveau-vente',
  templateUrl: './nouveauvente.component.html',
  styleUrls: ['./nouveauvente.component.css'],
  providers:[MessageService]
})
export class NouveauventeComponent implements OnInit {

  /***
   * 
   */

   msgs:Message[] = []; 
   venteForm: FormGroup; 

   typeventes:Select [] = undefined;
   typevente:Select = undefined;
   listventes:any = undefined;
   
   itemstypeventes:Select [] = undefined;
   itemstypevente:Select = undefined;

   penalite:boolean =false;
   disabled: boolean = true;
   //datevente:Date = {};


  /**
   * 
   * @param formBuilder 
   * @param messageService 
   * @param httpClient 
   * @param router 
   * @param tokenStorage 
   */
  constructor(private formBuilder: FormBuilder, private messageService: MessageService, private httpClient:HttpClient, private router:Router, 
    private tokenStorage:TokenStorageService)  { }


  ngOnInit(): void {

    this.venteForm  = this.formBuilder.group({
      //'nom': new FormControl('', Validators.required),
      'datevente': new FormControl('', Validators.required),
      'typevente': new FormControl('', Validators.required),
      'itemstypevente': new FormControl(''),
      'prix': new FormControl(''),
      'penalite': new FormControl(''),
      'penalitemontant': new FormControl(''),
    });


    this.httpClient.get(environment.url+"/api/postal/vente/parametrage/all",{headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe((response:any[])=>{
      this.typeventes = [];
      response.forEach(element => {
        this.typeventes.push({code:element, name:element['nom']});
      });

    }, error=>{
        this.showError(" une erreur c'est produit et le système selectionner le type de ventes - La raison est voici : "+error.message);
    });


    this.httpClient.get(environment.url+"/api/postal/vente/by/day",{headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe(response=>{
      this.listventes = response;
    }, error=>{
        this.showError(" une erreur c'est produit et le système selectionner le type de ventes - La raison est voici : "+error.message);
    });

  }

  change($event:any)
  {
    /**
     *  -- liste items 
     */
    this.itemstypeventes = [];
    $event.value.code['items'].forEach(element => {
        this.itemstypeventes.push({code:element, name:element['nom']});
    });

  }

  penaliteEvent($event)
  {
    console.log($event);
    console.log(this.penalite);
    if($event.checked)
    {
      this.disabled = false;
    }else{
      this.disabled = true;
    }

  }

  save(value:any)
  {
    console.log(value);

    const format = 'yyyy-MM-dd';    
    const format_date = 'dd';
    const locale = 'en-US';
   let vente: Vente = {};
   vente.typevente = value['typevente']['code'];
   
   
   vente.datevente= formatDate(value['datevente'],format,locale);
   if(value['itemstypevente'])
   {
     vente.itemstypevente= value['itemstypevente']['code'];
     
    vente.nom= value['itemstypevente']['name'];
   }else{
     vente.nom= value['typevente']['name'];
   }
   vente.prix= value['prix'];
   if(value['penalitemontant'])
   {
    vente.penalite= value['penalitemontant'];
   }

   this.httpClient.post(environment.url+"/api/postal/vente/save",vente, {headers: new HttpHeaders({
    'Authorization': 'Bearer '+this.tokenStorage.getToken()
    })
  }).subscribe((response)=>{
    
    this.venteForm.patchValue({type:undefined});
    this.venteForm.patchValue({prix:undefined});
    this.venteForm.patchValue({penalite:undefined});
    this.typevente = undefined;
    this.itemstypevente = undefined;

    //this.items = [];
    this.httpClient.get(environment.url+"/api/postal/vente/by/day",{headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe(response=>{
      this.listventes = response;
    }, error=>{
        this.showError(" une erreur c'est produit et le système selectionner le type de ventes - La raison est voici : "+error.message);
    });

    this.showSuccess("Vous avez enregistrer avec success votre colis  !!! ")
  }, error=>{
      this.showError(" une erreur c'est produit et le système n'a pas enregitré votre colis - La raison est voici : "+error.message);
  });


  }
  

  /**
   *  costumisation des erreurs
   */

   showSuccess(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
  }

  showInfo(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
  }

  showWarn(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
  }

  showError(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
  }


}
