import { formatDate } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NodeWithI18n } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Message, MessageService } from 'primeng/api';
import { ReceptionComponent } from 'src/app/apps/colis/reception/reception.component';
import { Dropdown } from 'src/app/apps/model/dropdown';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { environment } from 'src/environments/environment.prod';


export interface TypeVente{
  id?,
	
	 nom?,
	prix?,
	penalite?,
	
	items?,
	createdat?,	
	updatedat?,

}


export interface TypeVenteItems{
  id?,
	
  nom?,
 prix?,
 penalite?,
 
 items?,
 createdat?,	
 updatedat?,
}

@Component({
  selector: 'app-parametrage-vente',
  templateUrl: './parametrage-vente.component.html',
  styleUrls: ['./parametrage-vente.component.css'],
  providers:[MessageService]
})
export class ParametrageVenteComponent implements OnInit {

  /***
   * 
   */

   msgs:Message[] = []; 
   venteForm: FormGroup; 
   
   itemventeForm: FormGroup; 

   itemsventes:TypeVenteItems[] = [];


  /**
   * 
   * @param formBuilder 
   * @param messageService 
   * @param httpClient 
   * @param router 
   * @param tokenStorage 
   */
  constructor(private formBuilder: FormBuilder, private messageService: MessageService, private httpClient:HttpClient, private router:Router, 
    private tokenStorage:TokenStorageService)  { }


  ngOnInit(): void {

    this.venteForm  = this.formBuilder.group({
      'type': new FormControl('', Validators.required),
      'prix': new FormControl(''),
      'penalite': new FormControl(''),
    });

    this.itemventeForm  = this.formBuilder.group({
      'type': new FormControl('', Validators.required),
      'prix': new FormControl(''),
      'penalite': new FormControl(''),
    });


  }


  /**
   * 
   * @param typevente 
   */
  save(typevente:any)
  {
    let typeVente:TypeVente = {};

    typeVente.nom= typevente['type'];
    typeVente.prix= typevente['prix'];
    typeVente.penalite= typevente['penalite'];
    
    typeVente.items = this.items;


    this.httpClient.post(environment.url+"/api/postal/vente/parametrage/save",typeVente, {headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe((response)=>{
      
      this.venteForm.patchValue({type:undefined});
      this.venteForm.patchValue({prix:undefined});
      this.venteForm.patchValue({penalite:undefined});

      this.items = [];

      this.showSuccess("Vous avez enregistrer avec success votre colis  !!! ")
    }, error=>{
        this.showError(" une erreur c'est produit et le système n'a pas enregitré votre colis - La raison est voici : "+error.message);
    });

  }


      /**
     *  Partie item
     */
       public item:TypeVenteItems = undefined;
       public selecteditem:TypeVenteItems;
       newitem:boolean;
       public items:any = [];
       itemCols: any[];
       displayDialogitem=false;
       
       /**
        *  Partie item
        */   
       showDialogToAdditem() {
         this.newitem = true;
         this.item = {};
         this.displayDialogitem = true;
       }
   
       saveitem() {
           let items = [...this.items];
           if (this.newitem)
           {
             console.log(this.item);
             items.push(this.item);
           }
           else
           {
             items[this.items.indexOf(this.selecteditem)] = this.item;
           }
   
           this.items = items;
           this.item = null;
           console.log(this.items);
           this.displayDialogitem = false;
       }
   
       deleteitem() {  
           let index = this.items.indexOf(this.selecteditem);
           this.items = this.items.filter((val, i) => i != index);
           this.item = null;
           this.displayDialogitem = false;
       }
   
       onRowSelect(event) {
           this.newitem = false;
           
           //console.log(event);
   
           this.item =this.cloneitem(event.data);
   
           console.log(this.item);
           this.displayDialogitem = true;
       }
   
       cloneitem(c: TypeVenteItems): TypeVenteItems {
           let item = {};
           for (let prop in c) {
               item[prop] = c[prop];
           }
   
           console.log(item);
           return item;
       }
   

  

  /**
   *  costumisation des erreurs
   */

   showSuccess(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
  }

  showInfo(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
  }

  showWarn(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
  }

  showError(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
  }



}
