
import { Component } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Message,  MessageService } from 'primeng/api';
import { AuthService } from '../auth/auth.service';
import { AuthLoginInfo } from '../auth/models/login-info';
import { TokenStorageService } from '../auth/token-storage.service';

@Component({
  selector: 'app-login',
  templateUrl: './app.login.component.html',
  
  styleUrls: ['./app.login.component.scss'],
  providers:[MessageService]
})
export class AppLoginComponent {

  isLoggedIn = false;
  isLoginFailed = false;
  errorMessage = '';
  roles: string[] = [];
  private loginInfo: AuthLoginInfo;
  msgs: Message[] = [];

  authForm:FormGroup ;

  constructor(private auth:AuthService, private messageService: MessageService, private authService: AuthService, 
    private tokenStorage: TokenStorageService, private router:Router, private formbuilder:FormBuilder) { 

      }

  ngOnInit(): void {  

    if (this.tokenStorage.getToken()) {
      this.isLoggedIn = true;
      this.roles = this.tokenStorage.getAuthorities();
      this.router.navigate(['/accueil']); 
  
    }

    this.authForm = this.formbuilder.group({
      'username':new FormControl('', Validators.required),
      'password':new FormControl('', Validators.required)
    })
  }

  connexion(value:any)
  {
    
    this.loginInfo = new AuthLoginInfo(
      value['username'],
      value['password']);
 
    this.authService.attemptAuth(this.loginInfo).subscribe(
      data => {
        this.tokenStorage.saveToken(data.accessToken);
        this.tokenStorage.saveUsername(data.username);
        this.tokenStorage.saveAuthorities(data.authorities);
        
        this.isLoginFailed = false;
        this.isLoggedIn = true;
        this.roles = this.tokenStorage.getAuthorities();
        //this.reloadPage();
        this.router.navigate(['/accueil']); 
      },
      error => {
        this.errorMessage = error.error.message;
        this.showError(this.errorMessage );
        this.isLoginFailed = true;
      }
    );
  }
 
  reloadPage() {
    window.location.reload();
  }


  /**
   *  costumisation des erreurs
   */

  showSuccess(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
  }

  showInfo(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
  }

  showWarn(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
  }

  showError(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
  }


}

